.list {
    color: #ffff;
    width: 300px;
    background-color: #a33f44;
    height: 50px;
    margin :20px auto;
    border-radius: 5px;
}

.list p {
    position : relative;
    padding: 10px;
}
.list p span {
    position: absolute; 
    right: 10px;
}

.list p input {
    background-color: #a33f44;
    border:none;
    font-size: 20px;
}

.list p input:focus {
    outline:none;
}

.btn {
    background-color: #a33f44; 
    border: none;
    color: black; 
    margin: 0 5px;
    /* padding: 12px 16px; Some padding */
    font-size: 16px; 
    cursor: pointer; 
}
  
  /* Darker background on mouse-over */
.btn:hover {
background-color: rgb(140, 151, 185);
}

