body{
  background-color: rgb(61, 57, 57);
  /* text-justify: center; */
}


.App {
  background-color: rgb(163, 145, 145);
  min-height: 500px;
  width: 400px;
  margin: 20px auto;
}

#to-do-form input{
  background-color: rgb(92, 50, 50);
  border: 0;
  width: 200px;
  height: 50px;
  padding: 0 20px;
  margin: 30px;
  font-size: 18px;
  border-radius: 10px;
}

#to-do-form input::placeholder {
  color: rgba(245, 245, 245, 0.5);
}

#to-do-form input:focus {
  outline:none;
}

#to-do-form button {
  height: 50px;
  width: 80px;
  border-radius: 5px;
  border:0;
  background-color: rgb(202,156, 24);
  cursor: pointer;
  font-weight: bolder;
  color: #fff;
}


